let size = window.location.search.indexOf('size');
window.AJADev = true

if(window.isMobile && size==-1) {
  window.location.href = '/crm/mobile.html'
}


window.$ = window.jQuery = require('jquery')
window.moment = require('moment')

import 'bootstrap'
import 'datatables'
import '@/sass/app.scss'
import 'element-ui/lib/theme-chalk/index.css'

import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './stores'
import VeeValidate, { Validator } from 'vee-validate';
import vSelect from 'vue-select'
import VueLodash from 'vue-lodash'
import Toastr from 'vue2-toastr'
import VueCarousel from 'vue-carousel'
import VueClipboards from 'vue-clipboards'
import zh_TW from 'vee-validate/dist/locale/zh_TW'
import ElementUI from 'element-ui'

Vue.use(VueCarousel);
Vue.use(VueLodash, { name: 'lodash' })
Vue.use(VeeValidate, {events: 'change|custom'})
Vue.use(VueClipboards)
Vue.use(ElementUI)
Validator.localize('zh_TW', zh_TW);

Vue.use(Toastr, {
  type: 'top center',
  timeOut: 3000,
  wordWrap: true,
  width: '100%'
})
Vue.component('v-select', vSelect)

Vue.config.productionTip = false

import '../filters/commonFilter'

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
