import TabHeader from '../components/TabHeader'
import WarningModal from '../../desktop/components/WarningModal'
import { debuglog } from 'util';

export default {
  name: 'Index',
  mixins: [commonMixin],
  components: {
    TabHeader, DialOutPersonModal, DialInPersonModal, WarningModal
  },
  computed: {
    ...mapGetters([
      'News', 'Videos' , 'QueryKeyword'
    ])
  },
  data () {
    return {
      isSelect: false,
      isSelect2: false,
      selectItem: 0,
      selectItem2: 0,
      dialInModalOpen: false,
      dialOutModalOpen: false,
      WarningModalOpen: false,
      dialOutOpt: [
        {
          id: null,
          name: '請選擇或輸入',
          number: ''
        },
        {
          id: 1,
          name: 'AAA',
          number: 'FN52099'
        },
        {
          id: 2,
          name: 'BBB',
          number: 'FN52100'
        },
        {
          id: 3,
          name: 'CCC',
          number: 'FN52199'
        },
        {
          id: 4,
          name: 'DDD',
          number: 'FN52299'
        }
      ],
      dialInOpt: [
        {
          id: null,
          name: '請選擇或輸入',
          number: ''
        },
        {
          id: 1,
          name: 'AAA',
          number: 'FN52099'
        },
        {
          id: 2,
          name: 'BBB',
          number: 'FN52100'
        },
        {
          id: 3,
          name: 'CCC',
          number: 'FN52199'
        },
        {
          id: 4,
          name: 'DDD',
          number: 'FN52299'
        }
      ],
      dialOut: {
        person: null,
        insure_no: '',
        clientID: '',
        address: ['','',''],
        selected: []
      },
      dialIn: {
        person: null,
        insure_no: '',
        clientID: '',
        address: ['','',''],
        selected: []
      }
    }
  },
  methods: {
    ...mapActions([
      'loadNews', 'loadVideos' , 'setPageLock' , 'updateQueryKeyword'
    ]),
    updateDialoutForm (key, index) {
      // debugger
      console.log('updateDialoutForm')
      for(var i in this.dialOut) {
        if (i !== key) {
          if(i=='address')
            this.dialOut[i] = ['', '', '']
          else if (i=='person')
            this.dialOut[i] = ''
          else
            this.dialOut[i] = null
        }
      }
    },
    dialOutPersonAction (option) {
      if(this.dialOut.selected && this.dialOut.selected.length) return
      this.dialOut = {
        person: option.id,
        insure_no: '',
        clientID: '',
        address: ['','',''],
        selected: []
      }
    },
    dialInPersonAction (option) {
      if(this.dialIn.selected && this.dialIn.selected.length) return
      this.dialIn = {
        person: option.id,
        insure_no: '',
        clientID: '',
        address: ['','',''],
        selected: []
      }
    },
    submitDialOutModal (list) {
      this.dialOut = {
        person: '',
        insure_no: '',
        clientID: '',
        address: ['','',''],
        selected: null
      }

      let users = [
          {
              id: 1,
              name: '尚華強',
              no: 'FN59999',
              numbers: 456
          },
          {
              id: 2,
              name: '吳和龍',
              no: 'FN52099',
              numbers: 433
          },
          {
              id: 3,
              name: '吳孟達',
              no: 'FN52501',
              numbers: 211
          }
      ]

      this.dialOut.selected = list.reduce((array, item, index) => {
        let sel = users.find(user => item === user.id)
        array.push(`${sel.name} ${sel.no}`)
        return array
      }, [])
      this.panelClose('dialOutModalOpen')
      this.isSelect = true
      this.selectItem = 4
    },
    confirmChangeDailIn() {
      this.panelClose('WarningModalOpen')
      this.isSelect2 = true
      this.selectItem2 = 6
    },
    cancelChangeDailIn () {
      this.dialIn.selected = null
      this.panelClose('WarningModalOpen')
    },
    submitDialInModal (val) {
      this.panelClose('dialInModalOpen')
      this.dialIn = {
        person: '',
        insure_no: '',
        clientID: '',
        address: ['','',''],
        selected: []
      }
      let users = [
          {
              id: 1,
              name: '尚華強',
              no: 'FN59999',
              numbers: 456
          },
          {
              id: 2,
              name: '吳和龍',
              no: 'FN52099',
              numbers: 433
          },
          {
              id: 3,
              name: '吳孟達',
              no: 'FN52501',
              numbers: 211
          }
      ]
      this.dialIn.selected = users.reduce((array, item, index) => {
        if(item.id===val)
          array.push(`${item.name} ${item.no}`)
        return array
      }, [])

      this.panelOpen('WarningModalOpen')
      this.isSelect = true
      this.selectItem = 6
    },
    selectFormItem2(i) {
      this.isSelect2 = false
      if (i==6 && (!this.dialIn.selected || !this.dialIn.selected.length)) {
        return; 
      }
      this.isSelect2 = true
      this.selectItem2 = i
    },
    selectFormItem(i) {
      console.log('selectFormItem')
      this.isSelect = false
      if (i==4 && (!this.dialOut.selected || !this.dialOut.selected.length)) {
        return; 
      }
      this.isSelect = true
      this.selectItem = i
    }
  },
  beforeMount () {
  },
  mounted () {
  }
}
</script>
