<script>
import {mapActions, mapGetters} from 'vuex'
import {commonMixin} from '../../mixins/common'
import TabHeader from '../components/TabHeader'
import { debuglog } from 'util';

const dataTree = [
  {
    parent: null,
    id: 1,
    type: '專招',
    shouldComplete: 32827,
    notComplete: 121,
    ccmplete1: 0,
    ccmplete2: 0,
    ccmplete3: 0,
    touchRate1: 27.9,
    touchRate2: 0.0,
    touchRate3: 0.0,
    child: true
  },
  {
    parent: null,
    id: 2,
    type: '展業',
    shouldComplete: 98329,
    notComplete: 67975,
    ccmplete1: 30354,
    ccmplete2: 0,
    ccmplete3: 0,
    touchRate1: 30.9,
    touchRate2: 0.0,
    touchRate3: 0.0,
    child: true
  }
]

export default {
  name: 'TransferTab51',
  mixins: [commonMixin],
  components: {
    TabHeader
  },
  computed: {
    ...mapGetters([
      'News', 'Videos' , 'QueryKeyword'
    ]),
    tableRow () {
        if (this.currentRowID==0) return null
        this.dataTree = dataTree
        return this.dataTree.filter(row => row.parent == this.currentRowID)
    },
    breadcrumbs () {
      if(this.currentRowID==0)
        return []
      else
        return (this.getParentPath(this.currentRowID)).reverse()
    }
  },
  data () {
    return {
      form: {
        startYear: '',
        startMonth: '',
        endYear: '',
        endMonth: ''
      },
      order: {
          index: 1,
          asc: true
      },
      dataTree: [],
      currentRowID: 0,
    }
  },
  watch: {
      'searchType': 'changeData'
  },
  methods: {
    ...mapActions([
      'loadNews', 'loadVideos' , 'setPageLock' , 'updateQueryKeyword'
    ]),
    getParentPath (parent) {
      let path = []
      this.dataTree.forEach(row => {
        if (row.id == parent) {
          path.push({
            id: row.id,
            name: row.type
          })
          if (row.parent) {
            path = path.concat(this.getParentPath(row.parent))
          }
        }
      })
      // console.log(parent, path)
      return path
    },
    searchData () {
      this.currentRowID = null;
      // this.getPath(this.tableRow)
    },
    countTotal (key) {
      let list =  this[''] 
      if (!this.tableRow) return
      
      let num = 0
      this.tableRow.forEach(row => {
        num += row[key]
      })

      if(key.toLowerCase().indexOf('rate') > -1) {
        num =  num/this.tableRow.length
      }
      return num
    },
    changeData () {
        // Call API
      // this.breadcrumbs = []
      this.currentRowID = 0;
    }
  },
  beforeMount () {
  },
  mounted () {
  }
}
</script>
