    ]),
    getParentPath (parent) {
      let path = []
      this.dataTree.forEach(row => {
        if (row.id == parent) {
          path.push({
            id: row.id,
            name: row.type
          })
          if (row.parent) {
            path = path.concat(this.getParentPath(row.parent))
          }
        }
      })
      // console.log(parent, path)
      return path
    },
    searchData () {
      this.currentRowID = null;
      // this.getPath(this.tableRow)
    },
